'use client';

import React, { useState } from 'react';
import { ExternalLink, Github, Play, Award, Zap } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';

// Helper to avoid unsafe Tailwind dynamic class usage
const getColor = (color: string, opacity = '400') => {
  const map: Record<string, string> = {
    cyan: `text-cyan-${opacity}`,
    emerald: `text-emerald-${opacity}`,
    purple: `text-purple-${opacity}`,
    amber: `text-amber-${opacity}`,
    rose: `text-rose-${opacity}`,
    indigo: `text-indigo-${opacity}`,
  };
  return map[color] || 'text-white';
};

const PortfolioSection = () => {
  const [activeFilter, setActiveFilter] = useState('all');
  const [hoveredProject, setHoveredProject] = useState<number | null>(null);

  const filters = [
    { id: 'all', label: 'All Projects' },
    { id: 'web', label: 'Web Development' },
    { id: 'mobile', label: 'Mobile Apps' },
    { id: 'ai', label: 'AI Solutions' },
    { id: 'ecommerce', label: 'E-commerce' },
  ];

let projects: any[] = [];

  const filteredProjects =
    activeFilter === 'all'
      ? projects
      : projects.filter((project) => project.category === activeFilter);

  return (
    <section id="portfolio" className="py-20 px-4 relative">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="text-center mb-16">
          <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-gradient-to-r from-purple-900/40 to-pink-900/40 border border-purple-400/30 mb-8 cyber-border">
            <Award className="w-5 h-5 text-purple-400 animate-pulse" />
            <span className="text-purple-400 font-semibold text-sm tracking-wider space-grotesk">
              OUR PORTFOLIO
            </span>
          </div>

          <h2 className="text-5xl md:text-6xl font-bold orbitron mb-6">
            <span className="text-white">Showcasing </span>
            <span className="gradient-text">Innovation</span>
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto exo-2">
            Explore our cutting-edge projects powered by AI and bold design across industries.
          </p>
        </div>

        {/* Filters */}
        <div className="flex flex-wrap justify-center gap-4 mb-16">
          {filters.map((filter) => (
            <button
              key={filter.id}
              onClick={() => setActiveFilter(filter.id)}
              className={`px-6 py-3 rounded-lg font-semibold transition-all duration-300 cyber-border space-grotesk ${
                activeFilter === filter.id
                  ? 'bg-gradient-to-r from-purple-500/20 to-pink-500/20 text-purple-400 glow-box'
                  : 'bg-slate-900/40 text-gray-400 hover:text-purple-400 hover:bg-slate-800/50'
              }`}
            >
              {filter.label}
            </button>
          ))}
        </div>

        {/* Project Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredProjects.map((project) => (
            <div
              key={project.id}
              className={`cyber-border rounded-xl backdrop-blur-sm bg-slate-900/40 hover:bg-slate-800/50 transition-all duration-500 transform hover:scale-105 hover:-rotate-1 group overflow-hidden ${
                hoveredProject === project.id ? 'glow-box' : ''
              }`}
              onMouseEnter={() => setHoveredProject(project.id)}
              onMouseLeave={() => setHoveredProject(null)}
            >
              {/* Project Image Preview */}
              <div className="relative overflow-hidden">
                <div className={`w-full h-48 flex items-center justify-center bg-gradient-to-br from-${project.color}-900/30 to-slate-900/50`}>
                  <div className="text-center">
                    <div className={`w-16 h-16 bg-gradient-to-br from-${project.color}-400/30 to-purple-400/30 rounded-full flex items-center justify-center mx-auto mb-4 group-hover:scale-110 transition-transform`}>
                      <Zap className={`w-8 h-8 ${getColor(project.color)}`} />
                    </div>
                    <span className="text-gray-400 text-sm exo-2">Project Preview</span>
                  </div>
                </div>

                {/* Overlay Icons */}
                <div
                  className={`absolute inset-0 bg-black/80 flex items-center justify-center gap-4 transition-all duration-300 ${
                    hoveredProject === project.id ? 'opacity-100' : 'opacity-0'
                  }`}
                >
                  <Link href={project.links.live}>
                    <span className="p-3 bg-cyan-500/20 rounded-full cyber-border hover:bg-cyan-500/30 transition-all cursor-pointer">
                      <ExternalLink className="w-5 h-5 text-cyan-400" />
                    </span>
                  </Link>
                  <Link href={project.links.github}>
                    <span className="p-3 bg-purple-500/20 rounded-full cyber-border hover:bg-purple-500/30 transition-all cursor-pointer">
                      <Github className="w-5 h-5 text-purple-400" />
                    </span>
                  </Link>
                  <Link href={project.links.demo}>
                    <span className="p-3 bg-emerald-500/20 rounded-full cyber-border hover:bg-emerald-500/30 transition-all cursor-pointer">
                      <Play className="w-5 h-5 text-emerald-400" />
                    </span>
                  </Link>
                </div>
              </div>

              {/* Project Info */}
              <div className="p-6">
                <h3 className="text-xl font-bold text-white mb-3 space-grotesk group-hover:glow-text transition-all">
                  {project.title}
                </h3>
                <p className="text-gray-300 mb-4 exo-2 leading-relaxed">
                  {hoveredProject === project.id ? project.fullDescription : project.description}
                </p>

                {/* Tech Stack */}
                <div className="flex flex-wrap gap-2 mb-4">
                 {project.tech.map((tech: string, index: number) => (
                      <span
                      key={index}
                      className={`px-3 py-1 bg-slate-800/50 border rounded-full text-xs text-gray-300 cyber-border exo-2`}
                    >
                      {tech}
                    </span>
                  ))}
                </div>

                {/* Features (only when hovered) */}
                {hoveredProject === project.id && (
                  <div className="mb-4 animate-fade-in">
                    <h4 className="text-sm font-semibold text-cyan-400 mb-2 space-grotesk">KEY FEATURES</h4>
                    <div className="grid grid-cols-2 gap-1">
{project.features.map((feature: string, index: number) => (
                          <div key={index} className="flex items-center gap-2">
                          <div className={`w-1.5 h-1.5 ${getColor(project.color)} rounded-full animate-pulse`} />
                          <span className="text-xs text-gray-300 exo-2">{feature}</span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Stats */}
                {Object.entries(project.stats).map(([key, value], index) => (
  <div key={index} className="text-center">
    <div className={`text-lg font-bold ${getColor(project.color)} orbitron`}>
      {value as string | number}
    </div>
    <div className="text-xs text-gray-400 capitalize exo-2">{key}</div>
  </div>
))}

              </div>
            </div>
          ))}
        </div>

        {/* CTA */}
        <div className="text-center mt-16">
          <div className="cyber-border p-8 rounded-xl backdrop-blur-sm bg-gradient-to-br from-purple-900/20 to-pink-900/20">
            <h3 className="text-3xl font-bold text-white mb-4 orbitron">Have a Project in Mind?</h3>
            <p className="text-gray-300 mb-8 exo-2 text-lg">
              Let's collaborate to bring your vision to life with cutting-edge AI technology and innovative design.
            </p>
            <Link href="/contact">
              <button className="bg-gradient-to-r from-purple-500 to-pink-600 hover:from-purple-400 hover:to-pink-500 text-white px-8 py-4 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105 cyber-border glow-box space-grotesk">
                Start Your Project
              </button>
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
};

export default PortfolioSection;
